@charset "UTF-8";
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%; }

body {
  font-family: "Alice", serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.main {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1100px;
  width: 100%;
  padding: 0 16px 0;
  margin: 0 auto; }

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  border: 1px solid black;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat-Regular.ttf) format("ttf"), url(../fonts/Montserrat-Regular.woff) format("woff"), url(../fonts/Montserrat-Regular.woff2) format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal; }

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
  padding: 20px; }

/* Стили для контейнера модального окна */
.modal {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
  max-width: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  text-align: center; }

/* Стили для кнопки закрытия */
.close-btn {
  cursor: pointer;
  font-size: 18px;
  color: #333; }

.img-wrapper {
  max-width: 500px;
  width: 100%;
  height: 350px;
  position: relative;
  margin-bottom: 30px; }
  .img-wrapper img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    top: 0;
    left: 0; }

.section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh; }

.box {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start; }

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f8f8f8;
  margin: 0;
  /* display: flex;
    align-items: center;
    justify-content: center; */
  height: 100vh; }

.ipt-simple {
  width: 200px;
  padding: 10px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px; }

form {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  padding: 30px;
  /* max-width: 500px; */
  width: 100%; }

h2 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
  font-size: 38px; }

p {
  margin-bottom: 10px;
  color: #555;
  font-weight: bold; }

label {
  display: inline-block;
  margin-bottom: 15px;
  font-size: 16px; }

input {
  margin-right: 5px; }

button {
  background-color: #4caf50;
  color: #fff;
  padding: 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
  transition: background-color 0.3s ease; }

button:hover {
  background-color: #45a049; }
